import React from "react"
import { Link, graphql } from 'gatsby'

import Navbar from '../components/Navbar'
import Hero from '../components/Hero'
import LatestPosts from '../components/LatestPosts'
import FeaturedPost from '../components/FeaturedPost'
import Features from '../components/Features'
import Format from '../components/Format'
import Footer from '../components/Footer'

import { FaCode } from "react-icons/fa";

export default function Home({ data }) {

	const tutorials = data.allTutorialsJson.nodes;

	return (
		<>
			<Navbar/>
			<Hero/>
			<LatestPosts/>
			<TutorialSection tutorials={ tutorials }/>
			<Format/>
			<Footer/>
		</>
	)

}

const TutorialCard = function({ tutorial }) {

	const url = `/${tutorial.slug}/`;
	
	return (
		<div className="row">
			<div className="col-md-5 mb-4">
				<div className="bg-image shadow-sm">
					<img src={ tutorial.card_image} className="img-fluid rounded" />
				</div>
			</div>

			<div className="col-md-7 mb-4">
				<h5 className='display-6'>{ tutorial.title }</h5>
				<p className='lead'>{ tutorial.description }</p>
				<Link to={ url }>
					<button type="button" className="btn btn-primary btn-lg">Read More</button>
				</Link>
			</div>
		</div>
	)

}

function TutorialSection({ tutorials }) {

	console.log( tutorials );

	return (
		<section>
			<div className='container'>
				<h2 className='display-4 text-center my-4'>
					<span className='border-bottom'>Latest Tutorials</span>
				</h2>
				
				{tutorials.map( tutorial => {
					return <TutorialCard key={tutorial.id} tutorial={tutorial}/>
				})}
				
			</div>
		</section>
	)
}

export const query = graphql`
query GetLatest {
    allTutorialsJson(sort: {fields: id, order: DESC}, limit: 3) {
    nodes {
      slug
      id
      graphics_library
      card_image
      author
      avatar
      description
      title
      window_library
      repository
      language
      serial
    }
  }
}
`
